<template>
    <div>
        <div class="container">
            <div class="section base_width">
                <h3 class="section__tit">{{pagetitle}}</h3>
                <ul class="section__tab" v-if="post.categroup">
                    <li v-for="(i, index) in catelist" :key="index">{{i.name}} ({{i.count}})</li>
                </ul>
                <hr class="section__hr">
                <grid-view :catelist="this.catelist" :post="this.post" :items="this.items"></grid-view>
            </div>
        </div>
    </div>
</template>

<script>
import GridView from '@/components/GridView.vue'
import itemslist from '@/views/DataSet.json' 

export default {
    name: 'GoodsList',
    data: function(){
        return {
            items: itemslist.items,
            catelist: [
                {name:'전체', count:50, active:true},
                {name:'한식', count:8, active:false},
                {name:'양식', count:3, active:false},
                {name:'중식', count:3, active:false},
                {name:'일식', count:3, active:false},
                {name:'기타', count:3, active:false}
            ],
            post:{
                categroup: false,
                sortgroup: true,
                pagination: true,
                listoption: true
            },
        }
    },
    components:{
        GridView,
    },
    computed:{
        pagetitle : function() {
            let menunum = this.$route.params.id;
            
            if (menunum == 1){
                return '특가상품'
            }
            if (menunum == 2){
                return '신상품'
            }
            if (menunum == 3){
                return '이달의 행사'
            }
            return ''
        },
    }
}
</script>

